import { Component }   from '@angular/core';


@Component({
  template: `
  <div class="wrap">
    <div class="cr">
        <div class="content">
            <div class="title">Not Found 404</div>
        </div>
    </div>
   </div>
  `,
  styles: [`
        .wrap {
            margin: 0;
            padding: 0;
            width: 100%;
            height:100%;
            color: #B0BEC5;
            display: table;
            font-weight: 300;
        }
        .cr {
            text-align: center;
            display: table-cell;
            vertical-align: middle;
        }
        .content {
            text-align: center;
            display: inline-block;
        }
        .title {
            font-size: 72px;
            margin-bottom: 40px;
        }

  `]
})
export class NotFoundComponent {

}


