import { Component, AfterViewInit ,ElementRef, OnInit, ViewChild }   from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute }     from '@angular/router';
import { Http }     from '@angular/http';
import { GetEdge } from './soat.animation';

@Component({
  template: require('./app.component.html'),
  styles: [require('./app.component.css')]
})
export class AppComponent implements OnInit{

	@ViewChild('audioplayer') player: any;

	name: string;
  	license:string;
  	date:string;
  	action:string;
  	DOMaudio:any;
  	script:any;
  	audio:string;
  	valid : boolean = false;
  	audio_verify : boolean = false;
  	msg: string;

	constructor(private el:ElementRef, private router: ActivatedRoute, private http: Http, private sanitizer: DomSanitizer){}

	ngOnInit(){ 
		let _self = this;


		this.msg = 'Get parameters';

		this.name = this.router.snapshot.queryParams['name'];
		this.license = this.router.snapshot.queryParams['license'];
		this.date = this.router.snapshot.queryParams['date'];
		this.action = this.router.snapshot.queryParams['action'];
		this.audio = this.router.snapshot.queryParams['audio'];
		
		this.script = document.createElement("script");
	  	this.script.type = "text/javascript";
	  	this.script.innerHTML = GetEdge(this.name,this.license,this.date,"/js",this.action, this.audio);
	  	document.head.appendChild(this.script);

		this.msg = 'verify audio';

		this.player.nativeElement.addEventListener("error", function(){
			setTimeout(function(){
				_self.msg = 'audio not found';
				_self.VerifyAudio();
			},3000);
		});

		this.player.nativeElement.addEventListener("canplay", function(){
			_self.msg = 'can play';
			if(!_self.audio_verify){
				_self.PlayAnimation();
			}
			_self.audio_verify = true;
		});


		this.VerifyAudio();

	}

	VerifyAudio(){
		let _self = this;
		this.player.nativeElement.src = this.router.snapshot.queryParams['audio'];
		this.player.nativeElement.play();
	}

	public PlayAnimation(){
		let _self = this;
		this.player.nativeElement.pause();
		_self.msg = 'Animation load';
		if(_self.name && _self.license && _self.date){
			_self.msg = 'Finish load';
			setTimeout(function(){
				let _script = document.createElement("script");
				_self.valid = true;
				_script.type = "text/javascript";
				_script.innerHTML = `function playAnimation(){ animationStage.play(); } playAnimation();`;
				document.body.appendChild(_script);
			},8000);
		}else{
			_self.msg = 'parameters not found';
		}

	}


}


